html {
  font-size: 62.5%;
  min-width: 320px;
}
body {
  margin: 0;
  padding: 0;
  font-family: $mainFont;
  font-size: 16px;
  color: $gray;
}
* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $headingFont;
}

strong {
  color: $red;
}

p {
  font-family: "Lato-Regular", sans-serif;
  font-size: 1.6rem;
  line-height: 1.6;
  color: $gray;
}

a {
  text-decoration: none;
  color: $red;
}

.main {
  overflow-x: hidden;
}

.lazyload {
  opacity: 0;
}

.lazyloading {
  opacity: 1;
  transition: opacity 300ms;
  background: #f7f7f7 url("../images/loader.gif") no-repeat center;
}
